import styled from 'styled-components'

const H1 = styled.h1`

  font-size: 1.25rem;
  line-height: 1.5;
  text-align: center;
  position: relative;

  &:after{
    content: '';
    position: absolute;
    bottom: -.5rem;
    left: 0;
    right: 0;
    width: 100%;
    height: 1px;
    background: rgba(255,255,255,1);

    
  }

`

export default H1


